import React from 'react'
import { colors, sizes } from '../theme'
import StyledMenu from './StyledMenu'
import StyledButton from './StyledButton'
import Icon from 'antd/lib/icon'


class Header extends React.Component {
    state = {}

    componentDidMount() {
        this.setState({ menuToggleOpen: false })
    }

    handleMenuToggle = ({ }) => this.setState({ menuToggleOpen: !this.state.menuToggleOpen });

    render() {
        const { url } = this.props;

        return (
            <div>
                {this.props.width >= sizes.phone &&
                    <StyledMenu url={url} />
                }
                {this.props.width < sizes.phone &&
                    <StyledButton icon='bars' float='right' onClick={this.handleMenuToggle}/>
                }
                {(this.props.width < sizes.phone && this.state.menuToggleOpen) && <StyledMenu url={url} />}
            </div>
        )
    }
}


export default Header;



